.city-map {
  min-height: 350px;
  position: relative;
  z-index: 2;
}

.city-map__image {
  display: block;
  width: 100%;
  object-fit: cover;
  border: none;
}

.city-map__image--over {
  position: relative;
  z-index: 2;
}

.city-map__image--under {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
