.review-form__container {
  border-top: 1px solid @color-input-hover;
  padding: 24px 20px 39px;
  color: @color-basic-second;
}

.review-form__container--detailed {
  padding: 24px 20px 30px;
  margin-bottom: 19px;
}

.review-form__contacts {
  padding: 26px 20px 32px;
}

.review-form__impression .review-form__title {
  margin-bottom: 18px;
}

.review-form__impression {
  padding: 26px 20px 20px;
}

.review-form__sights-visited .review-form__title {
  margin-bottom: 18px;
}

.review-form__sights-visited {
  padding: 26px 20px 20px;
}

.review-form__detailed {
  border: none;
  padding: 0;
  margin: 0;
}

.review-form__group {
  border: none;
  margin: 0;
  padding: 0;
}

.review-form__group--data-in {
  display: grid;
  gap: 11px;
}

.review-form__title {
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 15px;
  text-align: center;
}

.review-form__checkradio {
  display: flex;
  flex-direction: column;
  gap: 12px;
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-weight: 700;
}

.review-form__footnote {
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.6);
  text-align: right;
  padding: 0 20px;
  margin: 3px 0 0 auto;
  max-width: 185px;
}

@media (min-width: @tablet-width) and (max-width: 1199px) {
  .review-form__item {
    width: calc(50% - 12px);
    box-sizing: border-box;
  }

  .review-form__item:nth-child(odd) {
    margin-right: 10px;
  }

  .review-form__item--attractions:nth-child(even) {
    padding-left: 7px;
  }
}

@media (min-width: @tablet-width) {
  .review-form {
    display: flex;
    flex-wrap: wrap;
  }

  .review-form__container {
    width: 100%;
    border-top: none;
  }

  .review-form__container--detailed {
    padding: 19px 20px 30px;
  }

  .review-form__group--has-border,
  .review-form--has-border {
    border-top: 2px solid @color-decoration-elements;
  }

  .review-form__group--has-border .review-form__title,
  .review-form--has-border .review-form__title {
    max-width: 325px;
    padding: 0 70px;
  }

  .review-form__initials {
    padding: 26px 43px 32px 20px;
  }

  .review-form__contacts {
    padding: 26px 20px 32px 44px;
  }

  .review-form__impression .review-form__title {
    margin-bottom: 28px;
  }

  .review-form__impression {
    padding: 30px 20px 20px;
  }

  .review-form__sights-visited .review-form__title {
    margin-bottom: 27px;
  }

  .review-form__detailed {
    padding: 0;
  }

  .review-form__container:last-of-type {
    margin-bottom: 12px;
  }

  .review-form__container--is-half {
    width: 50%;
    box-sizing: border-box;
  }

  .review-form__container--is-half:nth-child(odd) {
    position: relative;
  }

  .review-form__container--is-half:nth-child(odd)::before {
    content: "";
    position: absolute;
    display: block;
    width: 2px;
    height: 88%;
    background-color: @color-decoration-elements;
    right: 0;
    top: 25px;
  }

  .review-form__title {
    padding: 0;
    font-size: 21px;
    margin: 0 auto;
    margin-bottom: 24px;
    max-width: 400px;
  }

  .review-form__initials .review-form__title {
    padding-left: 41px;
  }

  .review-form__contacts .review-form__title {
    margin-left: -20px;
  }

  .review-form__checkradio {
    flex-direction: row;
    flex-wrap: wrap;
    padding: 0 36px;
  }

  .review-form__sights-visited .review-form__checkradio {
    padding: 0 72px 0 84px;
  }

  .review-form__footnote {
    font-size: 14px;
    padding: 20px 20px 0;
    max-width: 290px;
  }
}

@media (min-width: @desctop-width) {
  .review-form__container {
    padding: 26px 50px 39px 0;
  }

  .review-form__container--detailed {
    padding: 26px 0 32px;
  }

  .review-form__contacts {
    padding: 26px 0 32px 78px;
  }

  .review-form__container--is-half:nth-child(odd)::before {
    right: -14px;
  }

  .review-form__impression .review-form__title {
    margin-bottom: 43px;
  }

  .review-form__impression {
    padding: 34px 0;
    margin-bottom: 14px;
  }

  .review-form__impression .review-form__group {
    padding: 30 20px;
  }


  .review-form__sights-visited .review-form__title {
    margin-bottom: 59px;
  }

  .review-form__sights-visited {
    padding: 22px 0 36px;
    margin-bottom: 21px;
  }

  .review-form__sights-visited .review-form__group {
    padding: 0 22px 0 36px;
  }

  .review-form__container:last-of-type {
    margin-bottom: 10px;
  }

  .review-form__title {
    margin-bottom: 21px;
  }

  .review-form__initials .review-form__title {
    padding-left: 62px;
  }

  .review-form__contacts .review-form__title {
    margin-left: 30px;
  }

  .review-form__checkradio {
    padding: 0 33px;
    justify-content: flex-start;
    width: 100%;
    gap: 0;
  }

  .review-form__impression .review-form__item {
    width: calc(100% / 3);
  }

  .review-form__sights-visited .review-form__item {
    width: calc(100% / 4);
  }

  .review-form__sights-visited .review-form__checkradio {
    padding: 0 24px;
  }

  .review-form__footnote {
    margin-right: 76px;
  }
}
